<script>


export default {
	name: 'home',
	data(){
		return{
			isLogin:false,
			showGuessPopup:false,
			showPayPasswordPopup:false,
			payPw:'',
			user_info:{
				money:'238.23'
			},
			guess_data:{
				name:"8:20~8:30竞猜场次",
				up:0,
				up_meney:'',
				down:1,
				down_money:'500',
				end_time:'600'
			},
			most_data:{
				name:'小明',
				img:require('../assets/imgs/user.png'),
				money:'2300',
				times:10
			},
			record_data:{
				money:'2361',
				rank:'26',
				true: '12'
			},
			money_item:[20,100,200,500],
			money_item_index:-1,
			money:'',
			guess_type: '1',
			optionline:{
				xAxis: {
					type: 'category',
					show:false
				},
				yAxis: {
					type: 'value',
					show:false,
					min:'dataMin',
					max:'dataMax',
				},
				series: [{
					data: [820, 932, 901, 934,0, 1290, 1330, 1320,880,890,750,260,450,760],
					type: 'line',
					smooth: true,
					animation:false
				}],
				color:["#fff"]
				
			},
			btn_data:{
				btn_now:0,
				btn_pre:0,
				btn_percent:0,
				btn_up:false
			}
		}
	},
	created(){
		
		if(this.$route.params.id){
			this.utils.info('有id...已登录...');
			this.isLogin = true;
		}else{
			this.utils.info('没有id...未登录...');
			this.isLogin = false;
		}

	},
	mounted(){
		//获取btn信息
		this.getBtnFun();
	},
	methods:{
		//返回
		goBack(){
			this.utils.info('返回app...');
			this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
		},
		//去登录
		goLogin(){
			this.utils.info('去登录...');
			this.$bridge.callhandler('goLogin', {tag:"登录"}, (data) => {})
		},
		//战绩更多
		goMore(){
			this.utils.info('战绩更多');
			this.$bridge.callhandler('goMore', {tag:"战绩更多"}, (data) => {})
		},
		//玩法规则
		goRulePage(){
			this.$router.push({
				name:'rule'
			})
		},
		//显示验证密码
		showPayPassword(){
			this.showPayPasswordPopup = true;
		},
		//关闭验证密码
		closePayPassword() {
			this.showPayPasswordPopup = false
			this.payPw = '';

			this.utils.showToast('您放弃了支付');
		},
		// 数字键盘 事件
		onInput(key) {
			let that = this
			this.payPw = (this.payPw + key).slice(0, 6);
			if (this.payPw.length == 6) {
				let params = {
					payPass: that.payPw
				}
				
			}
		},
		onDelete() {
			this.payPw = this.payPw.slice(0, this.payPw.length - 1);
		},
		//猜涨/跌 type 1涨 2跌
		guessFun(type){
			if(this.isLogin){
				this.guess_type = type;
				this.showGuessPopup = true;
			}else{
				this.goLogin();
			}
			
		},
		//关闭猜涨/跌
		closeGuessFun(){
			this.showGuessPopup = false;
			this.money = '';
		},
		//确认猜涨跌
		confirmGuess(){
			
			this.closeGuessFun();
			this.showPayPassword();
		},
		//初始化图表
		drawLine(){
	
			let chartline = this.$echarts.init(this.$refs.homeChart);
			chartline.setOption(this.optionline);

		},
		//获取btn信息
		getBtnFun(){
			this.http.get('https://data.block.cc/api/v1/price/history?symbol_name=bitcoin&limit=50').then(result =>{
				this.utils.info('获取btn信息...',result);
				if(result.success){
					let btn_data = result.data;
					let line_data = [];
					for(let i=0;i<btn_data.length;i++){
						line_data.push(btn_data[i][1]);
					}

					this.utils.info('line_data...',line_data);
					this.optionline.series[0].data = line_data;

					this.btn_data.btn_now = line_data[0].toFixed(2);
					this.btn_data.btn_pre = line_data[1].toFixed(2);

					let num = this.btn_data.btn_now - this.btn_data.btn_pre;
					if(num > 0){
						this.btn_data.btn_up = true;
					}else{
						this.btn_data.btn_up = false;
					}
					this.btn_data.btn_percent = ((Math.abs(num))/(this.btn_data.btn_pre) * 100).toFixed(2);

					//初始化图表
					this.drawLine();
				}
			})
		}
	}
}
</script>


